import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export const store=new Vuex.Store({
    state:{
        isLogin:false
    },
    getters:{

    },
    mutations:{
        checkLogin(state,payload){
            state.isLogin=payload;
        }
    },
    actions:{
      loginAction({commit}){
        commit('checkLogin',true);
      }
    }
});



