<template>
  <div id="slider">
    <div class="row">
      <div class="col-md-4 col-sm-4 col-md-offset-3">
        <img src="../assets/head.jpg" alt="通过vue开发" style="width:100px;height:100px;border-radius: 50px;">
      </div>
    </div>
    <div class="row" style="margin:10px 0;">
      <div class="col-md-12 col-sm-12">
        <input type="text" class="form-control" placeholder="搜索博客" v-model="search">
      </div>
    </div>
    <div class="row">
      <div class="col-md-11 col-sm-11" style="margin-left:12px;">
        <ul class="nav nav-pills nav-stacked" v-for="blog in filteredBlogs" :key="blog.title">
            <li role="presentation" class="" v-on:mouseenter="changeBackground($event)" v-on:mouseleave="backColor($event)">
              <router-link v-bind:to="'/singleBlog/'+blog.id">{{blog.title | to-uppercase}}</router-link>
            </li>
        </ul>
      </div>
      <div class="col-md-11 col-sm-11">
        <router-link to="/add">
          <button class="btn btn-primary btn-block" style="margin: 10px 10px;">添加博客</button>
        </router-link>
      </div>
    </div>

  </div>

</template>

<script>
  import axios from '../axios-auth'
  export default {
    name: 'slider',
    data(){
      return{
        blogs:[],
        search:''
      }
    },
    created(){
      axios.get("/posts.json")
        .then(function(data){
          return data.data;
        })
        .then((data)=>{
          var blogsArray=[];
          for(let key in data){
            data[key].id=key;
            blogsArray.push(data[key]);
          }
          this.blogs=blogsArray;
        })
    },
    computed:{
      filteredBlogs:function(){
        return this.blogs.filter((blog)=>{
          return blog.title.match(this.search);
        })
      }
    },
    filters:{
      "to-uppercase":function(value){
        return value.toUpperCase();
      }
    },
    methods:{
      changeBackground:function (event) {
         event.currentTarget.className='active';
      },
      backColor:function (event) {
        event.currentTarget.className='';
      }
    }
  }
</script>

<style scoped>
  a{
    text-decoration: none;
  }
</style>



