<template>
  <div id="showblogs">
    <div class="row" >
      <div class="col-md-4 col-sm-4" v-for="blog in blogs" :key="blog.title">
        <h2>{{blog.title | to-uppercase}}</h2>
        <p>
          {{blog.content | snippet}}
        </p>
        <p>
            <router-link v-bind:to="'/singleBlog/'+blog.id"><a class="btn btn-default" href="#" role="button">详情 »</a></router-link>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
    export default {
        name: "show-blogs",
        data(){
          return{
            blogs:[]
          }
        },
        created(){
          axios.get("/posts.json")
            .then(function(data){
              //return data.json()
              return data.data;
            })
            .then((data)=>{
              var blogsArray=[];
              for(let key in data){
                data[key].id=key;
                blogsArray.push(data[key]);
              }
              this.blogs=blogsArray;
            })
        },
      filters:{
        "to-uppercase":function(value){
          return value.toUpperCase();
        },
        "snippet":function(value){
          return value.slice(0,100) + "...";
        }
      }
    }
</script>

<style scoped>

</style>



