<template>
  <div id="showblog">
    <div class="row">
      <div class="col-md-10 col-sm-10 blog-main">
        <div class="blog-post">
          <h2 class="blog-post-title">{{blog.title}} <router-link v-bind:to="'/edit/'+id"><button class="btn btn-primary">编辑</button></router-link></h2>
        </div>
      </div>
    </div>
    <div class="row">
      <div >
         <textarea readonly style="border: none;height:600px;" wrap="physical" class="col-md-12 col-sm-12 blog-main">
            {{blog.content}}
          </textarea>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
    export default {
        name: "show-blog",
        data(){
          return {
            id:this.$route.params.id,
            blog:{}
          }
        },
        created(){
          this.getData();
        },
       watch:{
          '$route':'getData'
       },
        methods:{
            getData(){
              axios.get('/posts/'+this.$route.params.id+'.json')
                .then(function (data) {
                  return data.data;
                })
                .then((data)=>{
                  this.blog=data;
                });
            }
        }
    }
</script>

<style scoped>
  .blog-main{
    line-height: 1.5;
    font-size:18px;
  }
</style>



