<template>
  <div id="editBlog">
    <div class="row">
      <div class="col-md-6 col-sm-6">
        <h2>博客标题</h2>
        <input type="text" class="form-control" v-model="blog.title">
      </div>
    </div>
    <div class="row">
      <h2 class="col-md-6 col-sm-6">博客内容</h2>
      <textarea class="col-md-12 col-sm-12 blog-main" wrap="physical" style="margin-left:10px;height:600px;" v-model="blog.content"></textarea>
    </div>
    <div class="row" style="margin: 10px 0">
      <div class="col-md-1 col-sm-1 col-md-offset-11 col-sm-offset-11">
        <button class="btn btn-primary btn-block" v-on:click.prevent="change">修改</button>
      </div>
    </div>
  </div>
</template>

<script>
    import axios from 'axios'
    export default {
        name: "edit-blog",
        data(){
          return{
            id:this.$route.params.id,
            blog:{}
          }
        },
        created(){
          axios.get('/posts/'+this.id+'.json')
            .then(function (data) {
              return data.data;
            })
            .then((data)=>{
              this.blog=data;
            });
        },
        methods:{
          change:function () {
              axios.put('/posts/'+this.id+'.json',this.blog)
                .then(()=>{
                  this.$router.push({path:'/showblogs'});
                })
          }
        }
    }
</script>

<style scoped>

</style>



