<template>
  <div id="login">
    <div class="row" id="alert" style="text-align: center">
      <div class="alert alert-warning alert-dismissible" role="alert">
        <button type="button" class="close" data-dismiss="alert" aria-label="Close"><span aria-hidden="true">&times;</span></button>
        <strong>Warning!</strong> <b id="text">登录后可进行后续操作！</b>
      </div>
    </div>
    <div class="row">
      <div class="col-md-6 col-sm-6 col-md-offset-3" v-on:click.prevent="hide">
        <h2>请先登录</h2>
        <label >用户名</label>
        <input type="text" class="form-control" v-model="username">
        <label >密码</label>
        <input type="password" class="form-control" v-model="password">
        <button class="btn btn-primary btn-block" style="margin: 10px auto;" v-on:click.prevent="checkLogin">登录</button>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapGetters,mapActions} from 'vuex'
    export default {
        name: "login",
        data(){
          return{
            username:'',
            password:''
          }
        },
        methods:{
          ...mapActions(['loginAction']),
          checkLogin:function () {
              if(this.username==='ribery' && this.password==='123456'){
                this.loginAction();
                this.$router.push('/');
              }else{
                this.show();
              }
          },
          show:function () {
            console.log(1);
            $('#alert').show();
            $('#text').text('用户名或密码错误！');
          },
          hide:function () {
            $('#alert').hide();
          }
        }
    }
</script>

<style scoped>

</style>



