<template>
    <div id="header">
      <div class="row">
        <div class="col-md-3 col-sm-3">
            欢迎来到Ribery的个人博客！
        </div>
        <div class="col-md-6 col-sm-6 col-md-offset-1">
          当前时间:{{getTime}}
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "blog-header",
        computed:{
            getTime:function () {
              let obj=new Date();
              let year=obj.getFullYear();
              let month=obj.getMonth()+1;
              let day=obj.getDate();
              let hour=obj.getHours();
              let min=obj.getMinutes();
              let date=obj.getDay();
              let week;
              switch (date){
                case 1:
                  week='一';
                  break;
                case 2:
                  week="二";
                  break;
                case 3:
                  week="三";
                  break;
                case 4:
                  week="四";
                  break;
                case 5:
                  week="五";
                  break;
                case 6:
                  week="六";
                  break;
                case 0:
                  week='天';
                  break;
              }
              let time=year+'年'+month+'月'+day+'日'+hour+':'+min+'星期'+week;
              return time;
            }
        }
    }
</script>

<style scoped>
.row{
  margin-top: 5px;
  border-radius: 4px;
  height:30px;
  background: #337ab7;
  color:#fff;
  line-height: 30px;
}
</style>



