<template>
  <div id="addBlog">
    <div class="row">
      <div class="col-md-6 col-sm-6">
        <h2>博客标题</h2>
        <input type="text" class="form-control" v-model="blog.title">
      </div>
    </div>
    <div class="row">
        <h2 class="col-md-6 col-sm-6">博客内容</h2>
        <textarea class="col-md-10 col-sm-10 blog-main" wrap="physical" style="margin-left:10px;height:600px;" v-model="blog.content"></textarea>
    </div>
    <div class="row" style="margin: 10px auto">
      <div class="col-md-1 col-sm-1 col-md-offset-9">
        <button class="btn btn-primary btn-block" v-on:click.prevent="post">提交</button>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'

    export default {
        name: "add-blog",
        data(){
          return {
            blog:{
              title:'',
              content:'',
              submitDate:''
            }
          }
        },
      methods:{
        post:function () {
          let obj=new Date();
          let year=obj.getFullYear();
          let month=obj.getMonth()+1;
          let day=obj.getDate();
          this.blog.submitDate=year+'-'+month+'-'+day;
          axios.post("/posts.json",this.blog)
            .then(()=>{
              this.$router.push({path:'/showblogs'});
            })
        }
      }
    }
</script>

<style scoped>
  .blog-main{
    line-height: 1.5;
    font-size:18px;
  }
</style>



